'use client';

import { useState, useEffect, FormEvent, ChangeEvent } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import { toast } from 'sonner';
import { Plus, Pencil, Trash2, Loader2, Search, X, ChevronLeft, ChevronRight, AlertTriangle } from 'lucide-react';

// Komponen UI
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

// --- TIPE DATA ---
interface Kategori { id: number; nama_kategori: string; }
interface Satuan { id: number; nama_satuan: string; }
interface BahanBaku {
    id: number;
    nama_bahan_baku: string;
    stok_saat_ini: number;
    batas_stok_minimum: number;
    foto_url: string;
    kategori_id: number;
    satuan_id: number;
    kategori?: Kategori;
    satuan?: Satuan;
}

export default function InventoryPage() {
    // State Data
    const [bahans, setBahans] = useState<BahanBaku[]>([]);
    const [kategoris, setKategoris] = useState<Kategori[]>([]);
    const [satuans, setSatuans] = useState<Satuan[]>([]);

    // State UI & Filter
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState('');
    const [filterKategori, setFilterKategori] = useState<string>('all');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10); // Stok biasanya banyak, tampilkan 10

    // State Modal
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editId, setEditId] = useState<number | null>(null);
    
    // Form State
    const [formData, setFormData] = useState({
        nama_bahan_baku: '',
        kategori_id: '',
        satuan_id: '',
        stok_saat_ini: '',
        batas_stok_minimum: '',
        foto: null as File | null,
    });

    // 1. Fetch Data
    const fetchAllData = async () => {
        setLoading(true);
        try {
            const [resBahan, resKat, resSat] = await Promise.all([
                axios.get('/api/bahanBakus'),
                axios.get('/api/kategoris'),
                axios.get('/api/satuans')
            ]);
            setBahans(resBahan.data);
            setKategoris(resKat.data);
            setSatuans(resSat.data);
        } catch (error) {
            toast.error("Gagal memuat data inventaris.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchAllData(); }, []);

    // 2. Filter & Pagination Logic
    const filteredBahans = bahans.filter((item) => {
        const matchName = item.nama_bahan_baku.toLowerCase().includes(searchQuery.toLowerCase());
        const matchCategory = filterKategori === 'all' || item.kategori_id.toString() === filterKategori;
        return matchName && matchCategory;
    });

    useEffect(() => { setCurrentPage(1); }, [searchQuery, filterKategori]);

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredBahans.slice(indexOfFirstItem, indexOfLastItem);
    const totalPages = Math.ceil(filteredBahans.length / itemsPerPage);

    // 3. Form Handlers
    const resetForm = () => {
        setFormData({
            nama_bahan_baku: '',
            kategori_id: '',
            satuan_id: '',
            stok_saat_ini: '',
            batas_stok_minimum: '',
            foto: null
        });
        setEditId(null);
        setIsDialogOpen(false);
    };

    const handleOpenDialog = (item?: BahanBaku) => {
        if (item) {
            setEditId(item.id);
            setFormData({
                nama_bahan_baku: item.nama_bahan_baku,
                kategori_id: item.kategori_id.toString(),
                satuan_id: item.satuan_id.toString(),
                stok_saat_ini: item.stok_saat_ini.toString(),
                batas_stok_minimum: item.batas_stok_minimum.toString(),
                foto: null
            });
        } else {
            resetForm();
        }
        setIsDialogOpen(true);
    };

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);

        try {
            if (editId) {
                // Update Teks
                await axios.put(`/api/bahanBakus/${editId}`, {
                    nama_bahan_baku: formData.nama_bahan_baku,
                    kategori_id: formData.kategori_id,
                    satuan_id: formData.satuan_id,
                    stok_saat_ini: formData.stok_saat_ini,
                    batas_stok_minimum: formData.batas_stok_minimum
                });
                // Update Foto
                if (formData.foto) {
                    const imgData = new FormData();
                    imgData.append('foto', formData.foto);
                    await axios.post(`/api/bahanBakus/${editId}/foto`, imgData);
                }
                toast.success("Bahan baku diperbarui!");
            } else {
                // Create Baru
                const data = new FormData();
                Object.keys(formData).forEach(key => {
                    // @ts-ignore
                    if (formData[key] !== null) data.append(key, formData[key]);
                });
                await axios.post('/api/bahanBakus', data);
                toast.success("Bahan baku ditambahkan!");
            }
            resetForm();
            const res = await axios.get('/api/bahanBakus');
            setBahans(res.data);
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal menyimpan.");
        } finally {
            setIsSaving(false);
        }
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Hapus bahan baku ini?")) return;
        try {
            await axios.delete(`/api/bahanBakus/${id}`);
            toast.success("Bahan baku dihapus");
            const res = await axios.get('/api/bahanBakus');
            setBahans(res.data);
        } catch {
            toast.error("Gagal menghapus.");
        }
    };

    return (
        <div className="space-y-6">
            {/* Header */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Stok Bahan Baku</h1>
                    <p className="text-gray-500 text-sm">Kelola inventaris dapur dan bar.</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={() => handleOpenDialog()}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Bahan
                </Button>
            </div>

            {/* Filter Area */}
            <Card className="border border-gray-200 shadow-sm">
                <CardContent className="p-4">
                    <div className="flex flex-col md:flex-row gap-4">
                        <div className="flex-1 relative">
                            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
                            <Input
                                placeholder="Cari bahan baku..."
                                className="pl-9"
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                            />
                        </div>
                        <div className="w-full md:w-[250px]">
                            <Select value={filterKategori} onValueChange={setFilterKategori}>
                                <SelectTrigger><SelectValue placeholder="Filter Kategori" /></SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="all">Semua Kategori</SelectItem>
                                    {kategoris.map((k) => (
                                        <SelectItem key={k.id} value={k.id.toString()}>{k.nama_kategori}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>
                        {(searchQuery || filterKategori !== 'all') && (
                            <Button variant="ghost" onClick={() => { setSearchQuery(''); setFilterKategori('all'); }}>
                                <X className="h-4 w-4 mr-2" /> Reset
                            </Button>
                        )}
                    </div>
                </CardContent>
            </Card>

            {/* Tabel */}
            <Card>
                <CardContent className="p-0">
                    <div className="rounded-md border">
                        <Table>
                            <TableHeader>
                                <TableRow className="bg-gray-50">
                                    <TableHead className="w-[50px] text-center">No</TableHead>
                                    <TableHead className="w-[80px]">Foto</TableHead>
                                    <TableHead>Nama Bahan</TableHead>
                                    <TableHead>Kategori</TableHead>
                                    <TableHead>Stok Saat Ini</TableHead>
                                    <TableHead>Min. Stok</TableHead>
                                    <TableHead className="text-right">Aksi</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {loading ? (
                                    <TableRow>
                                        <TableCell colSpan={7} className="h-24 text-center"><Loader2 className="h-5 w-5 animate-spin inline" /></TableCell>
                                    </TableRow>
                                ) : currentItems.length === 0 ? (
                                    <TableRow>
                                        <TableCell colSpan={7} className="h-24 text-center text-gray-500">Tidak ada data.</TableCell>
                                    </TableRow>
                                ) : (
                                    currentItems.map((item, index) => {
                                        // Cek apakah stok kritis (di bawah atau sama dengan batas minimum)
                                        const isCritical = Number(item.stok_saat_ini) <= Number(item.batas_stok_minimum);
                                        
                                        return (
                                            <TableRow key={item.id} className={`hover:bg-gray-50 ${isCritical ? 'bg-red-50' : ''}`}>
                                                <TableCell className="text-center">{(currentPage - 1) * itemsPerPage + index + 1}</TableCell>
                                                <TableCell>
                                                    <div className="relative h-10 w-10 rounded-md overflow-hidden bg-gray-100 border">
                                                        {item.foto_url ? (
                                                            <Image src={item.foto_url} alt={item.nama_bahan_baku} fill className="object-cover" unoptimized onError={(e) => (e.target as HTMLElement).style.display = 'none'} />
                                                        ) : <div className="w-full h-full flex items-center justify-center text-xs">IMG</div>}
                                                    </div>
                                                </TableCell>
                                                <TableCell className="font-medium">{item.nama_bahan_baku}</TableCell>
                                                <TableCell><span className="bg-blue-50 text-blue-700 px-2 py-0.5 rounded text-xs">{item.kategori?.nama_kategori}</span></TableCell>
                                                <TableCell>
                                                    <div className="flex items-center gap-2">
                                                        <span className={`font-bold ${isCritical ? 'text-red-600' : 'text-gray-900'}`}>
                                                            {item.stok_saat_ini} {item.satuan?.nama_satuan}
                                                        </span>
                                                        {isCritical && <AlertTriangle className="h-4 w-4 text-red-500 animate-pulse" />}
                                                    </div>
                                                </TableCell>
                                                <TableCell className="text-gray-500">{item.batas_stok_minimum} {item.satuan?.nama_satuan}</TableCell>
                                                <TableCell className="text-right space-x-2">
                                                    <Button variant="ghost" size="icon" onClick={() => handleOpenDialog(item)}><Pencil className="h-4 w-4 text-blue-600" /></Button>
                                                    <Button variant="ghost" size="icon" onClick={() => handleDelete(item.id)}><Trash2 className="h-4 w-4 text-red-600" /></Button>
                                                </TableCell>
                                            </TableRow>
                                        );
                                    })
                                )}
                            </TableBody>
                        </Table>
                    </div>

                    {/* Pagination (Sama seperti Produk) */}
                    {!loading && filteredBahans.length > 0 && (
                        <div className="flex items-center justify-between p-4 border-t">
                            <div className="text-sm text-gray-500">Total: {filteredBahans.length} Item</div>
                            <div className="flex space-x-2">
                                <Button variant="outline" size="sm" onClick={() => setCurrentPage(p => p - 1)} disabled={currentPage === 1}><ChevronLeft className="h-4 w-4" /></Button>
                                <span className="text-sm font-medium px-2 self-center">Hal {currentPage} / {totalPages}</span>
                                <Button variant="outline" size="sm" onClick={() => setCurrentPage(p => p + 1)} disabled={currentPage === totalPages}><ChevronRight className="h-4 w-4" /></Button>
                            </div>
                        </div>
                    )}
                </CardContent>
            </Card>

            {/* Modal Form */}
            <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
                <DialogContent className="sm:max-w-[600px]">
                    <DialogHeader>
                        <DialogTitle>{editId ? 'Edit Bahan Baku' : 'Tambah Bahan Baku'}</DialogTitle>
                    </DialogHeader>
                    <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                        <div className="grid grid-cols-2 gap-4">
                            <div className="space-y-2 col-span-2">
                                <Label>Nama Bahan <span className="text-red-500">*</span></Label>
                                <Input value={formData.nama_bahan_baku} onChange={(e) => setFormData({...formData, nama_bahan_baku: e.target.value})} required />
                            </div>
                            <div className="space-y-2">
                                <Label>Kategori <span className="text-red-500">*</span></Label>
                                <Select value={formData.kategori_id} onValueChange={(val) => setFormData({...formData, kategori_id: val})}>
                                    <SelectTrigger><SelectValue placeholder="Pilih..." /></SelectTrigger>
                                    <SelectContent>
                                        {kategoris.map((k) => <SelectItem key={k.id} value={k.id.toString()}>{k.nama_kategori}</SelectItem>)}
                                    </SelectContent>
                                </Select>
                            </div>
                            <div className="space-y-2">
                                <Label>Satuan <span className="text-red-500">*</span></Label>
                                <Select value={formData.satuan_id} onValueChange={(val) => setFormData({...formData, satuan_id: val})}>
                                    <SelectTrigger><SelectValue placeholder="Pilih..." /></SelectTrigger>
                                    <SelectContent>
                                        {satuans.map((s) => <SelectItem key={s.id} value={s.id.toString()}>{s.nama_satuan}</SelectItem>)}
                                    </SelectContent>
                                </Select>
                            </div>
                            <div className="space-y-2">
                                <Label>Stok Awal <span className="text-red-500">*</span></Label>
                                <Input type="number" value={formData.stok_saat_ini} onChange={(e) => setFormData({...formData, stok_saat_ini: e.target.value})} required />
                            </div>
                            <div className="space-y-2">
                                <Label>Batas Minimum (Alert)</Label>
                                <Input type="number" value={formData.batas_stok_minimum} onChange={(e) => setFormData({...formData, batas_stok_minimum: e.target.value})} />
                            </div>
                        </div>
                        <div className="space-y-2">
                            <Label>Foto</Label>
                            <Input type="file" accept="image/*" className="cursor-pointer" onChange={(e) => { if (e.target.files?.[0]) setFormData({ ...formData, foto: e.target.files[0] }); }} />
                        </div>
                        <DialogFooter className="pt-4">
                            <Button type="button" variant="outline" onClick={resetForm}>Batal</Button>
                            <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                                {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} Simpan
                            </Button>
                        </DialogFooter>
                    </form>
                </DialogContent>
            </Dialog>
        </div>
    );
}